@import url(https://fonts.googleapis.com/css2?family=Chakra+Petch:wght@700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  overflow: hidden;
  
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
#root{
  height: 100%;
  height: 100%;
  overflow: auto;
  background: linear-gradient( 109.6deg, var(--color-gradient-start)   16%, var(--color-gradient-end) 91.1% );
  height:100vh;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

:root{
    --color-primary:#000;
    --color-secundary:#fff;
    --color-gradient-start:rgba(5,84,94,1);
    --color-gradient-end:#bbb;
  }
  *{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  .wrapper{
    display: grid;
    grid-template-rows: 1fr 5fr 1fr;
    color: #fff;
    color: var(--color-secundary);
    grid-gap: 30px;
    gap: 30px;
    height: 100%;
  }
.header{
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    height: 80px;
}
.header img{
    height: 80%;
}
.header__menu{
    width: 4rem;
    display: flex;
    justify-content: center;
    text-transform: capitalize;
    position: relative;
}
details > summary {list-style-type: none;}
details >summary::-webkit-details-marker{display: none;}  
.header__menu ul{
    display: none;
    align-items: center;
    list-style: none;
    position: absolute;
    cursor: pointer;
    right: 0;
}
.header__menu a{
    text-decoration: none;
    color: var(--color-secundary);
}
.header__menu:hover ul{
    display:block;
}
.header__menu a:hover{
    text-decoration: underline;
}
@media only screen and (max-width: 400px){
    .header{
        flex-direction: column;
        height: auto;
    }
    .header img{
        height:auto;
        width: 100%;
    }
    .header__menu:hover .header__menu{
        margin-bottom: 20px;
    }
}
.footer{
    display: flex;
    align-items: center;
    justify-content: space-around;
    width: 100%;
    height: auto;
    background-color: rgba(0, 0, 0, .5);
    padding: 20px;
}
@media only screen and (max-width: 400px){
    .header{
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }
}
.page404-container{
    display: flex;
    justify-content: center;
    align-items: center;
}
.img-container{
    position: relative;
    width: 100px;
    height: 200px;
    -webkit-animation: moveUpToDown 3s infinite alternate;
            animation: moveUpToDown 3s infinite alternate;
}
.img-container>img{
    width: 100%;
    height: 100%;
}
.page404_title{
    font-family: 'Chakra Petch', sans-serif;
}
.btn-primary a{
    color: var(--colo-secundary);
    text-decoration: none;
}

@-webkit-keyframes moveUpToDown{
    0%{
        transform: translateY(0);
    }
    100%{
        transform: translateY(50%);
    }
}

@keyframes moveUpToDown{
    0%{
        transform: translateY(0);
    }
    100%{
        transform: translateY(50%);
    }
}
@-webkit-keyframes moveSideToSide{
    0%{
        transform: translateX(0);
    }
    100%{
        transform: translateX(100%);
    }
}
@keyframes moveSideToSide{
    0%{
        transform: translateX(0);
    }
    100%{
        transform: translateX(100%);
    }
}
@media only screen and (max-width:506px){
    .img-container{
        -webkit-animation: moveSideToSide 3s infinite alternate;
                animation: moveSideToSide 3s infinite alternate;
    }
}
.home{
    display: grid;
    grid-auto-rows: auto;
}
.searcher{
    padding: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
}
.searcher__title{
    margin-bottom: 20px;
    font-size: 2em;
}
.searcher__input{
    border-radius: 30px;
    width: 80%;
    padding: 10px;
    padding-left: 20px;
    background-color: rgba(255, 255, 255, .3);
    color: var(--color-secundary);
    max-width: 600px;
    outline: none;
}
.searcher__input:active{
    border: var(--color-secundary);
}
.searcher__input::-webkit-input-placeholder{
    color: var(--color-secundary);
    opacity: 1;
}
.searcher__input:-ms-input-placeholder{
    color: var(--color-secundary);
    opacity: 1;
}
.searcher__input::-ms-input-placeholder{
    color: var(--color-secundary);
    opacity: 1;
}
.searcher__input::placeholder{
    color: var(--color-secundary);
    opacity: 1;
}

.carousel__title{
    margin-left: 10px;
}
.carousel{
    width: 100%;
    overflow-x:scroll;
    overflow-y: hidden;
    padding: 0 10px;
    -ms-overflow-style: none;
}
/* .carousel::-webkit-scrollbar{
    height: 0;
    width: 0;
} */
.carousel__container{
    display: grid;
    grid-auto-flow: column;
    grid-auto-columns: 200px;
    grid-template-rows: 250px;
    margin: 70px 0;
    grid-gap: 10px;
    gap: 10px;
}
.carousel-item{
    width: 100%;
    height: 100%;
    position: relative;
    border-radius: 20px;
    background:aquamarine;
    overflow: hidden;
    cursor: pointer;
    will-change: width height;
    -webkit-transition: .4s ease-in-out ;
    transform-origin: center left;
}
.carousel-item__img{
    width: 100%;
    height: 100%;
    object-fit:cover;
}
.carousel-item:hover ~ .carousel-item{
    transform: translate3d(100px,0,0);
}
.carousel__container:hover .carousel-item{
    opacity: 0.3;
}
.carousel__container:hover .carousel-item:hover{
    transform: scale(1.5);
    opacity: 1;
}
@media only screen and (max-width:400px){
    .carousel__container{
        grid-auto-columns: 150px;
        grid-template-rows: 200px;
    }
    .carousel-item:hover ~ .carousel-item{
        transform: translate3d(75px,0,0);
    }
}
.carousel-item__description{
    display: flex;
    align-items: center;
    padding: 5px;
    width: 100%;
    justify-content: space-between;
    background: linear-gradient(to top, rgba(187,187,187,.8)50%,rgba(0,0,0,0)100%);
    position:absolute;
    bottom: 0;
    left: 0;
    opacity: 0;
    transition: .4s;

}
.carousel-item:hover .carousel-item__description{
    opacity: 1;
}
.carousel-item__description__tittles{
    display: flex;
    flex-wrap: nowrap;
    align-items: baseline;
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
}
.carousel-item__description__tittles--title{
    display: inline;
    font-size: .8em;
}

.logIn{
    width: 100%;
    display: flex;
    justify-content: center;
}
.logIn__container{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items:  center;
    width: 50%;
    margin: 0 auto;
    border: var(--color-secundary) 2px solid;
    border-radius: 20px;
    box-shadow: 0 5px 10px 3px rgba(0, 0, 0, 0.5);
    padding: 20px;
    background-image: linear-gradient( 109.6deg,  rgba(5,84,94,1) 16%, #bbb 91.1% );
    -webkit-animation: back-ground-animation 10s infinite alternate;
            animation: back-ground-animation 10s infinite alternate;
    background-size: 300%;
}
.logIn__container a{
    color: #05545E;
}
.logIn__container--form{
    width: 90%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items:  center;
}
.logIn__container--form input{
    width: 100%;
    border:none;
    border-bottom: 2px solid var(--color-secundary);
    background: none;
    padding: 10px;
    color:var(--color-secundary);
    margin-bottom: 20px;
}
.logIn__container--form button{
    border-radius: 20px;
    margin-bottom: 30px;
    background-color: rgba(0, 0, 0, 0.3);
    border: none;
}
.logIn-form__passwordOptions{
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    align-items: baseline;
    justify-content: space-between;
}
.logIn-form__passwordOptions label{
    display: flex;
    align-items: baseline;  
}
.logIn__container--social-media{
    align-self: start;
    padding: 50px 0;
}
.logIn-social-media__item{
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: baseline;
    margin-left: 10px;
}
@-webkit-keyframes back-ground-animation {
    0%{background-position:right top}
    100%{background-position:left bottom}
}
@keyframes back-ground-animation {
    0%{background-position:right top}
    100%{background-position:left bottom}
}
@media only screen and (max-width:600px){
    .logIn__container{
        width: 100%;
    }
}
